import React from 'react';
import styles from '../styles/Header.module.css';
import { Navbar, Nav, Form, FormControl, Container, Dropdown } from 'react-bootstrap';
import { FaSearch, FaBell, FaCog, FaUserCircle } from 'react-icons/fa';

const Header = () => {
  return (
    <Navbar bg="dark" expand="lg" className={`${styles.navbar} px-3`}>
      <Container fluid>
        {/* Brand/Logo */}
        <Navbar.Brand href="/" className={styles.logo}>
          <img src="/logo.png" alt="Shop Logo" className={styles.logoImg} />
          Fashion Admin
        </Navbar.Brand>

        {/* Toggle Button for Mobile */}
        <Navbar.Toggle aria-controls="navbar" />

        <Navbar.Collapse id="navbar">
          {/* Search Bar */}
          <Form className={`d-flex mx-auto ${styles.searchForm}`}>
            <FormControl
              type="search"
              placeholder="Search products, orders..."
              className={styles.searchInput}
              aria-label="Search"
            />
            <button className={styles.searchButton}>
              <FaSearch />
            </button>
          </Form>

          {/* Right Icons */}
          <Nav className="ms-auto d-flex align-items-center">
            {/* Notifications */}
            <Nav.Link href="#" className={styles.navIcon}>
              <FaBell />
              <span className={styles.badge}>4</span>
            </Nav.Link>

            

            {/* Profile Dropdown */}
            <Dropdown align="end">
              <Dropdown.Toggle as="div" className={styles.profileToggle}>
                <FaUserCircle className={styles.profileIcon} />
              </Dropdown.Toggle>
              <Dropdown.Menu>
                <Dropdown.Item href="/profile">Profile</Dropdown.Item>
                <Dropdown.Item href="/settings">Settings</Dropdown.Item>
                <Dropdown.Divider />
                <Dropdown.Item href="/logout">Logout</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default Header;
