import React, { useState } from 'react';
import styles from '../styles/Sidebar.module.css';
import { FaTachometerAlt, FaShoppingCart, FaTags, FaUsers, FaChartLine, FaCog } from 'react-icons/fa';

const Sidebar = () => {
  const [isCollapsed, setIsCollapsed] = useState(false);

  const toggleSidebar = () => {
    setIsCollapsed(!isCollapsed);
  };

  return (
    <div className={`${styles.sidebar} ${isCollapsed ? styles.collapsed : ''}`}>
      {/* Toggle Button */}
      <button className={styles.toggleButton} onClick={toggleSidebar}>
        {isCollapsed ? '→' : '←'}
      </button>

      {/* Logo */}
      <div className={styles.logo}>
        <img src="/logo.png" alt="Logo" />
        {!isCollapsed && <span>Fashion Admin</span>}
      </div>

      {/* Navigation Links */}
      <ul className={styles.navList}>
        <li className={styles.navItem}>
          <a href="/dashboard" className={styles.navLink}>
            <FaTachometerAlt />
            {!isCollapsed && <span>Dashboard</span>}
          </a>
        </li>
        <li className={styles.navItem}>
          <a href="/orders" className={styles.navLink}>
            <FaShoppingCart />
            {!isCollapsed && <span>Orders</span>}
          </a>
        </li>
        <li className={styles.navItem}>
          <a href="/products" className={styles.navLink}>
            <FaTags />
            {!isCollapsed && <span>Products</span>}
          </a>
        </li>
        <li className={styles.navItem}>
          <a href="/customers" className={styles.navLink}>
            <FaUsers />
            {!isCollapsed && <span>Customers</span>}
          </a>
        </li>
        <li className={styles.navItem}>
          <a href="/reports" className={styles.navLink}>
            <FaChartLine />
            {!isCollapsed && <span>Reports</span>}
          </a>
        </li>
        <li className={styles.navItem}>
          <a href="/settings" className={styles.navLink}>
            <FaCog />
            {!isCollapsed && <span>Settings</span>}
          </a>
        </li>
      </ul>
    </div>
  );
};

export default Sidebar;
